export default {
	onLaunch: function() {
		
			let _this = this;
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
			    if(loginRes.code){
					_this.$ajaxLogin('/wxLogin',"POST",{'code':loginRes.code}).then(res=>{
								uni.setStorageSync('token',res.token)
					})
				}else{
					
				}
			  }
			});
			
			
			
		
	},
	onShow: function() {
		
	},
	onHide: function() {
		console.log('App Hide')
	}
}
